.tbc-collapse .btn-collapse {
  background: transparent;
  border: none;
  border-bottom: 1px solid currentColor;
  color: currentColor;
  text-align: left;
}
.tbc-collapse .btn-collapse.collapsed {
  background: transparent;
  border: none;
  border-bottom: 1px solid currentColor;
}
.tbc-collapse .btn-collapse:hover {
  background: #00000015;
}

.tbc-container-example {
  min-height: 700px;
}

.tbc-background {
  background: #9eecb7;
  background: linear-gradient(180deg, #9eecb7 0%, #60b6ce 100%);
}